.services-card {
    padding-bottom: 45px;
    img {
        margin-right: 50px;
        width: 494px;
    }
    h1 {
        font-size: 20px;
    }
    h1:first-child {
        margin-top: 0;
    }
    .typography {
        p:first-child {
            margin-top: 0;
        }
    }
    .typography:first-child {
        margin-bottom: 45px;
        img {
            margin-top: 0;
        }
    }
    button {
        padding: 8px 35px;
        &:hover {
            color: var(--mainColor);
        }
    }
    .feedback {
        display: inline-block;
        width: 100%;
        &__inner {
            padding: 23px 44px;
        }
    }
}
@media(max-width: 1700px) {
    .services-card {
        .feedback {
            &__inner {
                padding: 23px 15px;
            }
        }
    }
}
@media(max-width: 767px) {
    .services-card {
        padding-bottom: 15px;
        .typography:first-child {
            margin-bottom: 25px;
        }
        .feedback {
            margin: 30px 0 25px;
        }
    }
}
@media(max-width: 425px) {
    .services-card {
        .Btn-Flamenco {
            width: 100%;
        }
    }
}